import ToolList from '../ToolList';
import ToolDetails from '../ToolDetails';
import { getTools } from '../../helpers/api';

export default {
  name: 'page-layout',
  components: {
    ToolList,
    ToolDetails,
  },
  created() {
    this.tools = getTools();
    this.selectedTool = this.tools.length ? this.tools[0] : null;
  },
  data() {
    return {
      showSnackbar: false,
      message: '',
      tools: [],
      selectedTool: null,
      editingTemplate: null
    };
  },
  watch: {
    message() {
      this.showSnackbar = true;
    }
  },
  methods: {
  }
};
